<template>
  <div id="bottom-navigation">
    <router-link tag="div" :to="item.className === 'home' ? '/' : item.className" class="btn" :class="item.className && active === item.className ? 'active' : ''" v-for="item in nav" :key="item.name">
      <ion-icon :name="$store.state.platform === 'ios' || $store.state.platform === 'ipados' || $store.state.platform === 'macos' ? item.className && active === item.className ? item.name : `${item.name}-outline` : item.name" class="btn-icon"></ion-icon>
      <div class="btn-text">{{ item.title }}</div>
    </router-link>
  </div>
</template>

<script>
import store from "@/store";

export default {
  name: "BottomNavigationMenu",
  props: {
    active: String,
    lang: String
  },
  data: () => ({
    nav: []
  }),
  mounted() {
    let lang = store.state.lang === 'ru'
    this.nav = [
      {
        className: 'home',
        name: 'home',
        title: lang ? 'Главное' : 'Asosiy'
      },
      {
        className: 'market',
        name: 'bag-handle',
        title: lang ? 'Маркет' : 'Market'
      },
      {
        className: 'partners',
        name: 'star',
        title: lang ? 'Спонсоры' : 'Homiylar'
      },
      {
        className: 'profile',
        name: 'person',
        title: lang ? 'Профиль' : 'Profil'
      },
    ]
  }
}
</script>

