<template>
  <div id="container">
    <div id="alert" class="error" :class="$store.state.premium_class">
      <ion-icon name="warning" class="icon"></ion-icon>
      <div class="title">{{ $store.state.lang === 'ru' ? 'Вы не оформили подписку!' : 'Obunani sotib olmagansiz!' }}</div>
      <div class="pay-btn" @click="subscripInvoice()">{{ $store.state.lang === 'ru' ? 'Оформить' : 'Bajarish' }}</div>
    </div>
    <HeaderUserBar />

    <div id="banner">
      <div class="banner-collage swiper">
        <div class="swiper-wrapper">
          <div class="swiper-slide">
<!--            <div class="item-gradient"></div>-->
            <img src="../files/slider_1_ru.svg" alt="" v-if="$store.state.lang === 'ru'"/>
            <img src="../files/slider_1_uz.svg" alt="" v-else/>
          </div>
          <div class="swiper-slide">
            <img src="../files/slider_2_ru.svg" alt="" v-if="$store.state.lang === 'ru'"/>
            <img src="../files/slider_2_uz.svg" alt="" v-else/>
<!--            <div class="item-gradient"></div>-->
          </div>
        </div>
      </div>
    </div>
    <div id="service">
      <div class="service-title">{{ $store.state.lang === 'ru' ? 'Другие сервисы' : 'Boshqa servislar'}}</div>
      <div class="swiper service-cards">
        <div class="swiper-wrapper">
          <router-link tag="div" to="/qr-code-cashback" class="service-card swiper-slide">
            <div class="service-pic">
              <img src="../files/cashback.svg" alt="Service Partners">
            </div>
            <div class="service-title">{{ $store.state.lang === 'ru' ? 'Кешбэк' : 'Keshbek' }}</div>
          </router-link>
          <a href="https://prizes.gamee.com/game-bot/karatekid2" class="service-card swiper-slide">
            <div class="service-pic">
              <img src="../files/game.svg" alt="Service Partners">
            </div>
            <div class="service-title">Ticket Game</div>
          </a>
          <router-link tag="div" to="/participation" class="service-card swiper-slide">
            <div class="service-pic">
              <img src="../files/buy_tickets.svg" alt="Service Partners">
            </div>
            <div class="service-title">{{ $store.state.lang === 'ru'? 'Учавствовать' : 'Qatnashish' }}</div>
          </router-link>
          <router-link tag="div" to="/give-a-dream" class="service-card swiper-slide">
            <div class="service-pic">
              <img src="../files/give_a_dream.svg" alt="Give a dream">
            </div>
            <div class="service-title">{{ $store.state.lang === 'ru' ? 'Подари мечту' : 'Orzu ulash' }}</div>
          </router-link>
        </div>
      </div>
    </div>

    <BottomNavigationMenu active="home" :lang="$store.state.lang === 'ru'" />
  </div>
</template>

<script>
import BottomNavigationMenu from "@/components/BottomNavigationMenu";
import HeaderUserBar from "@/components/HeaderUserBar";
import axios from "axios";

export default {
  name: "Home",
  components: {HeaderUserBar, BottomNavigationMenu},
  async mounted() {

    new Swiper(".banner-collage", {
      effect: "cards",
      grabCursor: true,
      autoplay: {
        delay: 2500,
      },
      slidesPerView: 'auto',
      loop: true,
    });

    new Swiper(".service-cards", {
      watchSlidesProgress: true,
      slidesPerView: 2.65,
    })


    Telegram.WebApp.MainButton.hideProgress();
    Telegram.WebApp.MainButton.hide();
    Telegram.WebApp.BackButton.hide();
  },
  methods: {
    async subscripInvoice()  {
      await axios.post(`https://server.gold-ticket.uz/api/invoice/${this.$store.state.user_id}`, {'lang': this.$store.state.lang})
          .then(responce => {
            if (responce.data.ok) {
              if (this.$store.state.lang === 'ru') {
                Telegram.WebApp.showPopup({
                  'title': '✅ Успешно',
                  'message': 'Бот вам отпрвил счёт для оплаты',
                  'buttons': [
                    {
                      'id': 'success',
                      'type': 'ok',
                      'text': "Продолжить"
                    }
                  ]
                })
              } else {
                Telegram.WebApp.showPopup({
                  'title': '✅ Muvofaqiyatli',
                  'message': 'Bot sizga tolov uchun hisob yubordi',
                  'buttons': [
                    {
                      'id': 'success',
                      'type': 'ok',
                      'text': "Davom etish"
                    }
                  ]
                })
              }
              Telegram.WebApp.onEvent('success', Telegram.WebApp.close())
            } else {
              if (this.$store.state.lang === 'ru') {
                Telegram.WebApp.showPopup({
                  'title': '⚠️Ошибка!',
                  'message': 'Произошло ошибка притязания счета оплаты\n\nПовторите попытку больше ',
                  'buttons': [
                    {
                      'id': 'success',
                      'type': 'ok',
                      'text': "Повторить"
                    }
                  ]
                })
              } else {
                Telegram.WebApp.showPopup({
                  'title': '⚠️Xato!',
                  'message': 'Tolov hisibi yaratishda hatolik yuz berdi!\n\nKeyinroq qayta urunib koring',
                  'buttons': [
                    {
                      'id': 'success',
                      'type': 'ok',
                      'text': "Davom etish"
                    }
                  ]
                })
              }
            }
          })
    },
  },
}
</script>
